$color-post-body-text: #313131;
$color-post-title-text: #424242;
$color-accent-blue: #2288f8;
$color-light-grey: #bebebe;
$font-azeretmono: "Azeret Mono", sans-serif;
$font-montserrat: 'Montserrat', sans-serif;
$font-emoji: 'Noto Color Emoji';
$--adaptiveGrey900: #191f28;

.content-wrap {
    padding: 26px 0 26px;
    display: flex;
    flex-direction: column;
    overflow: scroll;
    width: 70%;
}

.content-inner-box {
    border-radius: 20px;
    padding: 50px;
    background: #fff;
    color: $color-post-body-text;

    &+& {
        margin-top: 26px;
    }
}

// ÏøºÎ¶¨Í≥µÌÜµ 
.content-inner-box {

    .wp-block-post-title a {
        color: $color-post-title-text;
        text-decoration: none;
    }

    .wp-block-image {
        display: inline-block;
    }

    .wp-block-post-author,
    .wp-block-post-terms__separator {
        display: none; // Î∂àÌïÑÏöî
    }

    // ÎÇ†Ïßú
    .wp-block-post-date a {
        font-size: 17px;
        color: $color-post-body-text;
        text-decoration: none;
        pointer-events: none;
        cursor: default;
        color: $color-light-grey;
    }

    // ÌÉúÍ∑∏(ÎßÅÌÅ¨)
    .taxonomy-post_tag a {
        display: inline-block;
        font-family: $font-montserrat;
        padding-right: 12px;
        font-size: 15px;
        color: $color-light-grey;
        letter-spacing: -0.25px;
        text-decoration: none;

        &:hover {
            color: #242424;
        }
    }

    // Ïπ¥Îç∞Í≥†Î¶¨(ÎßÅÌÅ¨)
    .taxonomy-category a {
        font-family: $font-montserrat;
        font-size: 16px;
        text-transform: capitalize;
        color: $color-accent-blue;
        border-radius: 2px;
        padding: 6px;

        &:hover {
            background-color: $color-accent-blue;
            color: #fff;
        }
    }
}

// [/g] 'text-bubble' ÎßêÌíçÏÑ†Ïä§ÌÉÄÏùº
.wp-block-group.text-bubble {
    position: relative;
    margin: 60px 9% 190px 9%;
    padding: 26px 37px 26px 37px;
    font-family: $font-azeretmono;
    font-size: 20px;
    font-weight: 300;
    line-height: 1.8;
    letter-spacing: -0.2px;
    color: #fcfcfc;
    background: $color-accent-blue;
    border-radius: 34px;

    &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: -11px;
        margin-left: -6px;
        width: 0;
        height: 0;
        border-left: 8px solid transparent;
        border-right: 8px solid transparent;
        border-top: 12px solid $color-accent-blue;
    }

    &:before {
        content: "";
        position: absolute;
        bottom: -150px;
        left: 50%;
        margin-left: -65px;
        display: block;
        width: 130px;
        height: 130px;
        background: #f8f8f8;
        border-radius: 80px;
        background-image: url(https://self-affirm.com/wp-content/themes/aceofpen/assets/images/mimoji-congrats.png);
        background-size: 130px 130px;
    }
}

// [/g] 'text-sidenote' Ïä§ÌÉÄÏùº
.wp-block-group.text-sidenote {
    font-family: $font-azeretmono;
    font-size: 16px;
    letter-spacing: -0.2px;
    margin: 50px auto !important;
    padding: 20px 26px;
    box-shadow: 0 0 11px 0 rgba(0, 0, 0, 0.07);
    border-radius: 40px;
    line-height: 1.5;
    color: $--adaptiveGrey900;

    .wp-block-heading {
        margin-top: 0;
        font-size: 15px;

        &:before {
            content: 'üìå ';
            font-family: $font-emoji;
        }
    }
}

// Î≥∏Î¨∏ 
.content-inner-box {

    // ÌÉÄÏù¥ÌãÄ
    .wp-block-post-title a {
        text-decoration: none;
    }

    // Ïù¥ÎØ∏ÏßÄ
    .wp-block-image {
        overflow: hidden;
        border-radius: 4px;
    }

    // ÏÑúÎ∏åÌó§Îî©
    .wp-block-heading {
        margin-top: 42px;
    }
}

// Î≥∏Î¨∏ Î™©Î°ùÍ∏∞Î≥∏Ïä§ÌÉÄÏùº
.wp-block-post-content {
    ul {
        margin: 36px auto 42px;
        line-height: 1.7;

        li {
            &:before {
                content: '-';
                padding: 0 4px 0 2px;
            }
        }

        li~li {
            margin-top: 13px;
        }
    }
}

// Ïù∏Ïä§ÌÉÄ/Ìä∏ÏúÑÌÑ∞ ÏûÑÎ≤†Îìú
.instagram-media-rendered,
.twitter-tweet-rendered {
    margin: 14px auto !important;
}

// Ìã∞Ïò§Ïî®Ïä§ÌÉÄÏùº
.wp-block-aioseo-table-of-contents {
    margin: 50px auto !important;
    box-shadow: 0 0 11px 0 rgba(0, 0, 0, 0.07);
    border-radius: 40px;
    text-align: center;

    &:after {
        content: 'Dive Right Into';
        display: block;
        padding: 20px 14px;
        font-family: $font-montserrat;
        font-size: 21px;
        font-weight: 500;
        color: $color-post-title-text;
        border-top: 1px solid #eaeaea;
    }

    ul {
        margin: 0;
        line-height: 1.8;
        padding: 14px;
        counter-reset: my-counter;

        li {
            position: relative;
            padding-left: 10px;
            display: inline-block;

            a {
                display: inline-block;
            }

            &:before {
                content: counter(my-counter) "";
                position: absolute;
                left: -17px;
                top: 50%;
                margin-top: -13px;
                counter-increment: my-counter;
                padding: 0 7px;
                border: 1px solid #3d3d3d;
                min-width: 40px;
                min-height: 40px;
                border-radius: 30px;
            }
        }

        li~li {
            margin-top: 0;

        }

        a {
            display: inline-block;
            text-decoration: none;
            margin-top: 13px;
            word-wrap: break-word;
            font-family: $font-montserrat;
            font-size: 21px;
            font-weight: 500;
            letter-spacing: -0.2px;
            color: $color-accent-blue;

            &:hover {
                text-decoration: underline;
            }
        }
    }
}